<template>
	<div class="container">
		<div class="project" v-for='(work, index) in works' :key='index'>
			<div v-if='friendlyUrl(work.title) === proId'>
				<div class="project-heading" >
				    <div class="project-heading__title text-center">
				        {{work.title}}
				    </div>
				    <!-- /.project-heading__title -->
				    <div class="project-heading__buttons text-center">
				        <button class="project-heading__button button" :class='{activeBtn: desktop}' @click='changeFrame("desktop")'>
				            <img src="./icons/desktop.svg" alt="">
				        </button>
				        <button class="project-heading__button button" :class='{activeBtn: tablet}' @click='changeFrame("tablet")'>
				            <img src="./icons/tablet.svg" alt="">
				        </button>
				        <button class="project-heading__button button" :class='{activeBtn: mobile}' @click='changeFrame("mobile")'>
				            <img src="./icons/mobile.svg" alt="">
				        </button>
				    </div>
				    <div class="project-heading__frame" :class='{desktop, mobile, tablet}' :style='{"backgroundImage": `url(${work.img})`}' @click='load = true'>
				    	<div class="locked" v-if='!load'>
				    		Хочете переглянути сайт не залишаючи цієї сторінки? <br> Клікніть мишкою де-небудь у вікні, яке ви бачите перед собою.
				    	</div>
				    	<!-- /.locked -->
				        <iframe v-else :src="work.url"  class="frame" frameborder="0"></iframe>
				    </div>
				    <!-- /.project-heading__frame -->
				</div>
				<!-- /.project-heading -->
					
				<div class="row">
				    <div class="col-12 col-lg-7">
				        <div class="project-info">
				            <div class="project-info__title">
				                Опис проекту
				            </div>
				            <!-- /.project-info__title -->
				            <div class="project-info__descr" v-html='work.describe'>
				            </div>
				            <!-- /.project-info__descr -->
				            <div class="project-info__buttons">
				                <a :href="work.url">
				                    <button class="project-info__button show button">
				                        Дивитись проект
				                    </button>
				                </a>
				                <!-- /.project-info__button show-->
				            </div>
				            <!-- /.project-heading__buttons -->
				        </div>
				        <!-- /.project-heading -->
				    </div>
				    <!-- /.col-8 -->
				    <div class="col-12 col-lg-5">
				        <div class="project-meta">
				            <div class="project-meta__date project-meta__item">
				                <span>Термін розробки:</span>
				                <br>{{work.deadline}}
				            </div>
				            <!-- /.project-info__date -->
				            <div class="project-meta__client project-meta__item">
				                <span>Клієнт:</span>
				                <br>{{work.client}}
				            </div>
				            <!-- /.project-info__client -->
				            <div class="project-meta__skills project-meta__item">
				                <span>Технології:</span>
				                <br> {{work.tech}}
				            </div>
				            <!-- /.project-info__skills -->
				
				            <!-- /.project-info__meta -->
				        </div>
				        <!-- /.project-info -->
				    </div>
				    <!-- /.col-4 -->
				</div>
				<!-- /.row -->
			</div>
		</div>
		<!-- /.project --> 
	</div>
	<!-- /.container -->
</template>
<script>
	import { db } from '@/main';
	import cyrillicToTranslit from 'cyrillic-to-translit-js';

	export default {
		data () {
			return {
				proId: this.$route.params.title,
				desktop: true,
				tablet: false,
				mobile: false,
				load: false,
				works: []
			}
		},
		methods: {
			changeFrame(device){
				this.desktop = this.tablet = this.mobile = false;
				switch (device) {
					case 'tablet':
						this.tablet = true
						break;
					case 'mobile':
						this.mobile = true
						break;
					default:
						this.desktop = true;
						break;
				}
			},
			friendlyTitle(value) {
				value = value.replace(/_/g, " ");
				return value;
			},
			friendlyUrl(value) {
			    return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
			},
		  	
		},
		firestore () {
			 	return {
			   		works: db.collection('works').orderBy('createdAt')
			 	}
		  	},
		
	}
</script>
