import Vue from "vue";
import Vuex from "vuex";

import {auth} from '@/main';
import { db } from './main';
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
  	isUser: JSON.parse(localStorage.getItem('logged')) || false,
    articles: [],
    db,
  },
  mutations: {
    isLogIn(state) {
      state.isUser = true;
      localStorage.setItem('logged', JSON.stringify(state.isUser));
    },
    isLogOut(state) {
      state.isUser = false;
      localStorage.setItem('logged', JSON.stringify(state.isUser));

    } 
  },
  actions: {
    
  },
  getters: {
  	User: state => {
      return state.isUser;
    }
  }
});
