        <div class="blog-article" v-for='article in articles' :key='article.index'>
            <div v-if='article.id === proId'>
                <FormPost :article='article' />
            </div>
        </div>
        <!-- /.menu-item -->
	</div>
</template>
<script>
import { db } from '@/main';
import FormPost from './FormPost';
export default {
