import firebase from 'firebase/app';
import 'firebase/auth';

import Vue from "vue";
import Router from "vue-router";

import Home from "./views/Home.vue";
import Portfolio from "./views/Portfolio.vue";
import Blog from "./views/Blog.vue";
import Services from "./views/Services.vue";

import Admin from "./views/Admin.vue";

import NotFound from "./views/NotFound.vue";

import PortfolioSingle from "./components/portfolio/PortfolioSingle.vue";
import BlogSingle from "./components/blog/BlogSingle.vue";

import NewWork from "./components/portfolio/NewWork.vue";
import NewPost from "./components/blog/NewPost.vue";

import EditPost from "./components/blog/EditPost.vue";
import EditWork from "./components/portfolio/EditWork.vue";
import EditDraft from "./components/blog/EditDraft.vue";


import Login from "./components/auth/Login.vue";

import Meta from 'vue-meta';

Vue.use(Router);
Vue.use(Meta);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
      meta: {
        title: 'Головна | Юрій Колцьо',
        metaTags: [
          {
            name: 'description',
            content: 'The home page of Yurii Koltso site.'
          },
          {
            property: 'og:description',
            content: 'The home page of Yurii Koltso site.'
          }
        ]
      },
    },
    {
      path: "/portfolio",
      name: "portfolio",
      component: Portfolio,
      meta: {
        title: 'Портфоліо | Юрій Колцьо',
        metaTags: [
          {
            name: 'description',
            content: 'The portfolio page of Yurii Koltso site.'
          },
          {
            property: 'og:description',
            content: 'The portfolio page of Yurii Koltso site.'
          }
        ]
      },
    },
    {
      path: "/portfolio/:title",
      name: "single-work",
      component: PortfolioSingle,
      props: true
    },
    {
      path: "/blog/:title",
      name: "single-article",
      component: BlogSingle,
      props: true
    },
    {
      path: "/blog",
      name: "blog",
      component: Blog,
      meta: {
        title: 'Блог | Юрій Колцьо',
        metaTags: [
          {
            name: 'description',
            content: 'The blog page of Yurii Koltso site.'
          },
          {
            property: 'og:description',
            content: 'The blog page of Yurii Koltso site.'
          }
        ]
      },
    },
    {
      path: "/new_work",
      name: "new_work",
      component: NewWork,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        const currentUser = firebase.auth().currentUser;
        const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

        if (requiresAuth && !currentUser) next('login');
        /*else if (!requiresAuth && currentUser) next('admin');*/
        else next();
      }
    },
    {
      path: "/new_post",
      name: "new_post",
      component: NewPost,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        const currentUser = firebase.auth().currentUser;
        const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

        if (requiresAuth && !currentUser) next('login');
        /*else if (!requiresAuth && currentUser) next('admin');*/
        else next();
      }
    },
    {
      path: "/admin",
      name: "admin",
      component: Admin,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        const currentUser = firebase.auth().currentUser;
        const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

        if (requiresAuth && !currentUser) next('login');
        /*else if (!requiresAuth && currentUser) next('admin');*/
        else next();
      }
    },
    {
      path: "/edit_post",
      name: "edit_post",
      component: EditPost,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        const currentUser = firebase.auth().currentUser;
        const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

        if (requiresAuth && !currentUser) next('login');
        /*else if (!requiresAuth && currentUser) next('admin');*/
        else next();
      }
    },
    {
      path: "/edit_draft",
      name: "edit_draft",
      component: EditDraft,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        const currentUser = firebase.auth().currentUser;
        const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

        if (requiresAuth && !currentUser) next('login');
        /*else if (!requiresAuth && currentUser) next('admin');*/
        else next();
      }
    },
    {
      path: "/edit_work",
      name: "edit_work",
      component: EditWork,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        const currentUser = firebase.auth().currentUser;
        const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

        if (requiresAuth && !currentUser) next('login');
        /*else if (!requiresAuth && currentUser) next('admin');*/
        else next();
      }
    },
    {
      path: "/login",
      name: "login",
      component: Login
    },
    {
      path: '/services',
      name: 'services',
      component: Services,
      meta: {
        title: 'Послуги | Юрій Колцьо',
        metaTags: [
          {
            name: 'description',
            content: 'The services page of Yurii Koltso site.'
          },
          {
            property: 'og:description',
            content: 'The services page of Yurii Koltso site.'
          }
        ]
      },
    },
    {
      path: '*',
      name: 'empty',
      component: NotFound
    }
  ]
});
