<template>
	<div class="container">
		<article v-for='(article, index) in articles' :key='index' class="blog-article">
			<div v-if='friendlyUrl(article.title) === proId'>
				<div class="blog-article__img">
				    <img :src="article.img" alt="" class="img-responsive">
				</div>
				<!-- /.blog-article__img -->
				<div class="blog-article__meta">
				    <span class="date">
				        <i class="fas fa-calendar-alt"></i>
				       {{article.createdAt.seconds | moment("DD.MM.YY")}}
				    </span>
				</div>
				<!-- /.blog-article__meta -->
				<div class="blog-article__title">
				    {{article.title}}
				</div>
				<!-- /.blog-article__title -->
				<div class="blog-article__content" v-html='article.content'></div>
				<!-- /.blog-article__content -->
				<div class="blog-article__share">
					<p>
						Сподобалась публікація? Поділись нею з друзями :)
					</p>
					<facebook
					    :page_url='currentUrl'
					    title_social="Facebook"
					    has_icon
					  ></facebook>
				  	<telegram
			      		:page_url='currentUrl'
			      		title_social="Telegram"
			      		has_icon
			      	></telegram>
			      	<twitter
			      		:page_url='currentUrl'
			      		title_social="Twitter"
			      		has_icon
			      	></twitter>
				</div>
				<!-- /.blog-article__share -->
				<div class="comments">
				    <vue-disqus shortname="personal-12" :identifier="article.id" :url="currentUrl"></vue-disqus>
				  </div>
			</div> 
		</article>
		<!-- /.blog-article -->
	</div>
	<!-- /.container -->
</template>

<script>
	import Facebook from "vue-goodshare/src/providers/Facebook.vue";
	import Telegram from "vue-goodshare/src/providers/Telegram.vue";
	import Twitter from "vue-goodshare/src/providers/Twitter.vue";
	import cyrillicToTranslit from 'cyrillic-to-translit-js';

	import { db } from '../../main';
	export default {
		data() {
			return {
				proId: this.$route.params.title,
				articles: [],
				currentUrl: window.location.href,
				current: ''
			}
		},
		metaInfo () {
	      	return {
	        	title: this.currentArticle().title,
	       		meta: [
	       			{
	       				name: 'robots',
	       				content: 'index,follow,noodp'
	       			},
	       			{
	       				name: "googlebot",
	       				content: "index,follow"
	       			},
	       			{
	       				property: 'og:locale',
	       				content: 'uk_UK',
	       			},
	       			{
	       				property: 'og:type',
	       				content: 'article',
	       			},
	       			{
	       				property: 'og:title',
	       				content: this.currentArticle().title,
	       			},
	       			{
	       				property: 'og:url',
	       				content: this.currentUrl,
	       			},
	       			{
	       				property: 'og:site_name',
	       				content: 'Сайт Юрія Колцьо',
	       			},
	          		{ 
	          			property: 'og:description', 
	          			content: this.currentArticle().excerpt 
	          		},
	          		{	
	          			property: 'og:image',
	          			content: this.currentArticle().img 
	          		},
	          		{
	          			property: 'twitter:image',
	          			content: this.currentArticle().img 
	          		},
	          		{
	          			property: 'twitter:title',
	          			content: this.currentArticle().title,
	          		},
	          		{
	          			property: 'twitter:description',
	          			content: this.currentArticle().excerpt,
	          		},
	        	]
	      }
	    },
		methods: {
			friendlyUrl(value) {
	  			return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
			},
			currentArticle() {
				this.articles.forEach((item) => {
					if (this.friendlyUrl(item.title) === this.proId) {
						this.current = item;
					}
				});
				return this.current;
			}
		},
		computed: {
			
		},
		firestore () {
		   return {
		     articles: db.collection('blog').orderBy('createdAt')
		   }
		},
		components: {
		   Facebook,
		   Telegram,
		   Twitter
		}
		
	}
</script>
