<template>
	<footer class="footer">
	    <div class="footer-text">
	        <div class="copyright">&copy; <a class="footer-text__link" href="#">Yuriy Koltso.</a> {{new Date().getFullYear()}} </div>
	    </div>
	    <!-- /.footer-text -->
	    <!-- /.container -->
    	<div class="enter" v-if='User'>
    		<div  @click='logout' class="logout">
    			Вийти
    			<img src="@/components/auth/icons/sign-out.svg" alt="">
    		</div>
    		<!-- /.logout -->
    	</div>
	</footer>
	<!-- /.footer -->
</template>
<script>
	import {auth} from '@/main';

	export default {
		methods: {
			logout() {
				auth.signOut()
					.then(() => {
						this.$router.push({path: '/'});
						this.$store.commit('isLogOut');
					})
			}
		},
		computed: {
			User() {
				return this.$store.getters.User;
			}
		},
	}
</script>
