<template>
	<section class="blog-manage">
		<div class="section-title">
		    <h2 class="section-title__text" @click='toggle'>Керування чернетками</h2>
		</div>
		<!-- /.section-title -->
		<div class="post-list" v-if='show'>
			<div class="empty-list" v-if='drafts.length === 0'>
				Нема чернеток
			</div>
			<div v-else class="table-content table-responsive">
			    <table>
			        <thead>
			            <tr class="title-top">
			                <th>Назва</th>
			                <th>Дата публікації</th>
			                <th>Дії</th>
			            </tr>
			        </thead>
			        <tbody>
			            <tr v-for='draft in drafts' :key='draft.id'>
			                <td>
			                	<img :src="draft.img" class="img-responsive" alt="product img" />
			                </td>
			                <td>
			                	{{draft.title}}
			                </td>
			                
			                <td class="actions">
			           			<router-link
			           				tag='button' class='btn btn-primary' 
									:to="{ name: 'edit_draft', params: { title: friendlyUrl(draft.title), id: draft.id }}"
								>
									Редагувати
								</router-link>
			                	<button class='btn btn-primary'  @click='deleteItem(draft.id)'>Видалити чернетку</button>
			                </td>
			            </tr>
			        </tbody>
			    </table>
			</div>
			<div class="admin-content__add text-right">
				<router-link to='/new_post' tag='button' class='btn btn-primary'>Нова чернетка</router-link>
			</div>
			<!-- /.admin-content__add -->
		</div>
	</section>
	
</template>
<script>
	import { db } from '@/main'
	import cyrillicToTranslit from 'cyrillic-to-translit-js';

	export default {
		name: 'draftManage',
		data() {
			return {
				drafts: [],
				show: false,
			}
		},
		methods: {
			friendlyUrl(value) {
	  			return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
			},
			deleteItem(id) {
				db.collection('draft_post').doc(id).delete()
			},
			toggle() {
				this.show = !this.show;
			}
		},
		firestore () {
		   return {
		     drafts: db.collection('draft_post')
		   }
		},
	}
</script>