<template>
	<section class="blog-manage">
		<div class="section-title">
		    <h2 class="section-title__text" @click='toggle'>Керування роботами</h2>
		</div>
		<!-- /.section-title -->
		<div class="post-list" v-if='show'>
			<div class="empty-list" v-if='works.length === 0'>
				Нема робіт!
			</div>
			<div v-else class="table-content table-responsive">
			    <table>
			        <thead>
			            <tr class="title-top">
			                <th>Назва</th>
			                <th>Дата публікації</th>
			                <th>Дії</th>
			            </tr>
			        </thead>
			        <tbody>
			            <tr v-for='work in works' :key='work.id'>
			                <td>
			                	<img :src="work.img" class="img-responsive" alt="product img" />
			                </td>
			                <td>
			                	{{work.title}}
			                </td>
			                
			                <td class="actions">
			           			<router-link
			           				tag='button' class='btn btn-primary' 
									:to="{ name: 'edit_work', params: { title: friendlyUrl(work.title), id: work.id }}"
								>
									Редагувати
								</router-link>
			                	<button class='btn btn-primary'  @click='deleteItem(work.id)'>Видалити</button>
			                </td>
			            </tr>
			        </tbody>
			    </table>
			</div>
			<div class="admin-content__add text-right">
				<router-link to='/new_work' tag='button' class='btn btn-primary'>Нова робота</router-link>
			</div>
			<!-- /.admin-content__add -->
		</div>
	</section>
	
</template>
<script>
	import { db } from '@/main'

	export default {
		name: 'portfolioManage',
		data() {
			return {
				works: [],
				show: false,
			}
		},
		methods: {
			friendlyUrl(value) {
		  		value = value.replace(/\s/g, "_");
		  		return value;
			},
			deleteItem(id) {
				db.collection('works').doc(id).delete()
			},
			toggle() {
				this.show = !this.show;
			}
		},
		firestore () {
		   return {
		     works: db.collection('works').orderBy('createdAt')
		   }
		},
	}
</script>