<template>
	<div class="container">
		<div class="row"></div>
		<h1>Додати новий запис(чернетку)</h1>
			<form @submit.prevent="addPost(item)">
				<div class="row">
					<div class="col-12">
						<div class="form-group">
						    <label for="title">Назва запису</label>
						    <input type="text" v-model='item.title' class="form-control" id="title">
						</div>
					</div>
					<!-- /.col-12 col-lg-6 -->					
					<div class="col-12">
						<div class="form-group">
					    	<label for="pre">Прев'ю запису:</label>
					    	<PreviewImage @getImg='getSrc' />
					  	</div>
					</div>
					<!-- /.col-12 -->				
				</div>
				<div class="form-group">
			    	<label for="excerpt">Короткий опис:</label>
			    	<textarea name="excerpt" class="form-control" v-model='item.excerpt' id="excerpt"></textarea>
			  	</div>
			  	<div class="form-group">
			    	<label for="describe">Контент:</label>
					<vue-editor for='describe' v-model="item.content"></vue-editor>
			  	</div>
				<button class="btn btn-primary" @click='addToDraft(item)'>Додати до чернеток</button>
				<button class="btn btn-primary" type="submit">Додати роботу</button>
			</form>	
	</div>
</template>
<script>
	import { db } from '@/main';
	import { VueEditor } from 'vue2-editor';
	import PreviewImage from '@/components/PreviewImage';
	export default {
		name: 'NewPost',
		data() {
			return {
				item: {
					title: '',
					img: '',
					excerpt: '',
					content: ''
				},		
			}
		},
		components: {
		      VueEditor,
		      PreviewImage
		   },
		methods: {
		   	addPost (item) {      // <-- новый метод
		     	const createdAt = new Date();
		     	db.collection('blog').add({ ...item, createdAt })
		   	},

		   	getSrc (data) {
		        this.item.img = data;
		    },
		    addToDraft(item) {      // <-- новый метод
		     	const createdAt = new Date();
		     	db.collection('draft_post').add({ ...item, createdAt })
		   	},
		 }
	}
</script>
<style scoped>
	input, select, textarea {
		display: block;
	}
</style>